
@font-face {
font-family: 'Star Wars';
src: url('/assets/fonts/sw.ttf') format('truetype');
}
$padding: 1em;
$banner-size: 200px;
$avatar-size: 50px;
$dark-blue : hsl(203.18deg 33.85% 25.49%);
$black: hsl(0deg 0% 17.65%);
$amber: hsl(35.21deg 48.59% 48.82%);
$light-blue: hsl(195deg 20.87% 45.1%);
$brown: hsl(30deg 42.47% 28.63%);
$background-color: $black;
$header-background: $dark-blue;
$text-color: $amber;
$sw-font: 'Star Wars', Arial, sans-serif;
$font-stack: Arial, sans-serif;
body {
    font-family: $font-stack;
    background-color: $background-color;
    color: $text-color;
    margin: 0;
    padding: 0;
    font-weight: lighter;
}
h1{
font-weight: lighter;
 font-size: 4em;
 font-family: $font-stack;
}
h2 {
 font-size: 2em;
 font-weight: lighter;
 font-family: $sw-font;
}
a {
    text-decoration: none;
    color: inherit;
}

header {
    background-color: $header-background;
    padding: $padding;
    text-align: center;
    position: relative;

    h1 {
        margin: 0;
        font-family: $sw-font;
        font-size: 2em;
        font-weight: lighter;
    }

    .banner {
       height: $banner-size;
       width: $banner-size;
       background-image: url('/assets/images/banner.png'); /* Replace with Jawa image URL */
       background-size: cover;
       background-position: center;
       margin: 0 auto;
       border-radius: 50%;
    }
}

.container {
    padding: $padding;
}

.team {
    margin-bottom: 2em;

    h2 {
        margin: 0.5em 0;
    }

    .characters, .counters {
        list-style-type: none;
        padding: 0;
        display: flex;
        flex-wrap: wrap;

        li {
            display: flex;
            align-items: center;
            margin: 0.5em;

            img {
                width: $avatar-size;
                height: $avatar-size;
                border-radius: 50%;
                margin-right: 0.5em;
            }
        }
    }
}

.dropdown {
    margin-top: 0.5em;

    button {
        background-color: $header-background;
        color: $text-color;
        padding: 0.5em 1em;
        border: none;
        cursor: pointer;
    }

    .dropdown-content {
        display: none;
        background-color: $background-color;
        padding: 0.5em;

        ul {
            padding: 0;
        }
    }
}

.video {
    margin-top: 1em;

    iframe {
        width: 100%;
        height: 315px;
    }
}
